.checkout-page {
  .tab-content {
    margin-top: $padding-large-vertical * 3;
  }

  .payment-methods .card-flag:after {
    display: inline-block;
    width: 40px;
    height: 37px;
    content: '';
    vertical-align: middle;
  }

  .payment-methods {
    dt {
      margin-bottom: 5px;
    }

    dd {
      margin-bottom: $padding-large-vertical * 2;
    }

    .bank-flag:after {
      display: inline-block;
      width: 40px;
      height: 26px;
      border-radius: $border-radius-base;
      content: '';
      vertical-align: middle;
    }

    .bank-flag.bb:after,
    .bank-flag.banco_do_brasil:after {
      background-position: 246px -6px;
    }

    .bank-flag {
      &.bradesco:after {
        background-position: 144px -6px;
      }

      &.santander:after {
        background-position: 42px -6px;
      }

      &.itau:after {
        background-position: 94px -6px;
      }

      &.banrisul:after {
        background-position: 196px -6px;
      }
    }

    .boleto-flag:after {
      display: inline-block;
      width: 40px;
      height: 32px;
      background-position: 298px -4px;
      content: '';
      vertical-align: middle;
    }
  }

  .payment-methods .card-flag.mastercard:after,
  .payment-methods .card-flag.master:after {
    background-position: -53px 0;
  }

  .payment-methods .card-flag.visa:after {
    background-position: -4px 0;
  }

  .payment-methods .card-flag.amex:after,
  .payment-methods .card-flag.american_express:after {
    background-position: -105px 0;
  }

  .payment-methods .card-flag.dinners:after,
  .payment-methods .card-flag.diners_club:after {
    background-position: -156px 0;
  }

  .payment-methods .card-flag.hipercard:after {
    background-position: -259px 0;
  }

  .payment-methods .card-flag.elo:after {
    background-position: -207px 0;
  }

  .payment-methods .card-flag.hiper:after {
    background-position: -307px 0;
  }

  .payment-methods .card-flag.discover:after {
    width: 47px;
    background-position: -663px 0;
  }

  .payment-methods .card-flag.aura:after {
    width: 38px;
    background-position: -720px 0;
  }

  .payment-methods .card-flag.jcb:after {
    width: 38px;
    background-position: -768px 0;
  }

  .card-flag:after,
  .bank-flag:after,
  .boleto-flag:after {
    background: url('data:image/png;base64,');
  }
}

@import 'checkout/security-widget';
@import 'checkout/cart';
@import 'checkout/payment';
@import 'checkout/success';
@import 'checkout/wizard';
@import 'checkout/complete';
